<!--suppress JSUnresolvedReference -->
<script>
import storage from "@/utils/storage";

export default {
  name: "CoffeeHeader",
  data() {
    return {
      lang: 'en',
      langList: [
        {value: 'tc', name: '繁'},
        {value: 'sc', name: '简'},
        {value: 'en', name: 'ENG'}
      ],
      productList: [
        {
          img: require("@/assets/images/餐食配图/贝果系列/1.jpeg"),
          title: "LIGHT MEAL",
          target: 'blank',
          href: '/coffee/gourmet'
        },
        {
          img: require("@/assets/images/饮品&咖啡商品配图/咖啡商品/7.jpg"),
          title: "COFFEE",
          target: 'scroll',
          href: 'secondPage'
        },
        {
          img: require("@/assets/images/饮品&咖啡商品配图/现制咖啡饮品/7.jpg"),
          title: "FRESH COFFEE ",
          target: 'scroll',
          href: 'threePage'
        },
      ],
      showMobileNav: false,
      showMobileProduct: false
    }
  },
  computed: {
    langName() {
      return this.langList.find(item => item.value === this.lang).name;
    }
  },
  watch: {
    $route() {
      this.showMobileNav = false;
    }
  },
  mounted() {
    this.selectLangHandle(storage.getLanguage());
  },
  methods: {
    selectLangHandle(type) {
      if (type === 'undefined' || !type) return;
      this.lang = type;
      this.$i18n.locale = type;
      storage.setLanguage(type);
    },
    scrollTo(name) {
      this.showMobileNav = false;
      if (this.$route.path === '/coffee/home') {
        document.getElementById(name).scrollIntoView({
          behavior: "smooth",// 过渡动画效果 详细看文档
          block: "start" // 滚动到的位置 可选start center end
        });
      } else {
        this.$router.push({path: '/coffee/home', query: {scroll: name}})
      }
    },
    moveTp({target, href}) {
      if (target === 'blank') {
        this.$router.push(href)
      } else {
        console.log(this.$route.path)
        if (this.$route.path === '/coffee/home') {
          document.getElementById(href).scrollIntoView({
            behavior: "smooth",// 过渡动画效果 详细看文档
            block: "start" // 滚动到的位置 可选start center end1
          });
        } else {
          this.$router.push({path: '/coffee/home', query: {scroll: href}})
        }
      }
    }
  }
}
</script>

<template>
  <div class="w-full  border-b border-gray-200 absolute top-0 left-0 z-50 text-coffee bg-gray-50 bg-opacity-5">
    <div class="container mx-auto h-20 flex items-center justify-between px-5 sm:px-10">
      <div @click="$router.push('/home')" class="cursor-pointer order-2 sm:order-1">
        <img src="@/assets/visit.png" alt="logo" class="h-16"/>
      </div>
      <div class="sm:block hidden sm:order-2 h-full">
        <ul class="flex h-full items-center nav">
          <li :class="{'text-coffeeLight':$route.path === '/coffee/home'}" @click="$router.push('/coffee')">
            {{ $t('coffee.header.nav.home') }}
          </li>
          <li :class="{'text-coffeeLight':$route.path === '/coffee/gourmet'}"
              @mouseenter="$bus.$emit('productHover',true)"
              @mouseleave="$bus.$emit('productHover',false)">
            <span>{{ $t('coffee.header.nav.products') }}</span>
            <i class="el-icon-arrow-down ml-1"></i>
          </li>
          <li @click="scrollTo('fourPage')">{{ $t('coffee.header.nav.shop') }}</li>
          <li :class="{'text-coffeeLight':$route.path === '/coffee/boutique'}"
              @click="$router.push('/coffee/boutique')">
            {{ $t('coffee.header.nav.boutique') }}
          </li>
          <li :class="{'text-coffeeLight':$route.path === '/coffee/history'}" @click="$router.push('/coffee/history/')">
            {{ $t('coffee.header.nav.history') }}
          </li>
          <li @click="scrollTo('sixPage')">{{ $t('coffee.header.nav.more') }}</li>
        </ul>
      </div>
      <div class="sm:hidden order-1">
        <i class="el-icon-more  block text-coffee text-3xl" @click="showMobileNav = !showMobileNav"></i>
      </div>
      <div class="order-3">
        <el-dropdown @command="selectLangHandle">
          <span class="flex items-center">
            <img src="@/assets/icon/language.png" alt="">
            <span class="px-1">{{ langName }}</span>
            <i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item v-for="item in langList" :key="item.value" :command="item.value">
              <p :class="{'text-coffeeLight': item.value === lang}">{{ item.name }}</p>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
    <el-drawer
        title=""
        modal-append-to-body
        append-to-body
        size="60%"
        :visible.sync="showMobileNav"
        direction="ltr">
      <div class="flex flex-col h-full nav-mobile">
        <div :class="{'text-coffeeLight':$route.path === '/coffee/home'}" @click="$router.push('/coffee')">Home</div>
        <div class="cursor-pointer" :class="{'text-coffeeLight':$route.path === '/coffee/gourmet'}"
             @click="showMobileProduct = !showMobileProduct">
          <span>products</span>
          <i class="el-icon-arrow-down ml-1"></i>
        </div>
        <div class=" hover-li px-5  transition-all overflow-hidden" :class="[showMobileProduct?'max-h-96':'max-h-0']">
          <div class="flex flex-col w-full py-5">
            <div v-for="item in productList" :key="item.title" class="flex items-center my-2 product-item w-full px-3"
                 @click="moveTp(item)">
              <div class="w-20 h-16">
                <img class="w-full h-full" :src="item.img" alt="">
              </div>
              <p class="ml-2 text-sm whitespace-nowrap">{{ item.title }}</p>
            </div>
          </div>
        </div>
        <div @click="scrollTo('fourPage')">Shop</div>
        <div :class="{'text-coffeeLight':$route.path === '/coffee/boutique'}"
             @click="$router.push('/coffee/boutique')">
          Boutique
        </div>
        <div :class="{'text-coffeeLight':$route.path === '/coffee/history'}" @click="$router.push('/coffee/history/')">
          History
        </div>
        <div @click="scrollTo('sixPage')">More</div>
      </div>
    </el-drawer>
  </div>
</template>

<style scoped lang="scss">
.language-selector {
  background-color: orange;
  color: black;
  border-radius: 5px;
}

.nav {
  li {
    padding: 0 1.5rem;
    cursor: pointer;
    height: 100%;
    display: flex;
    align-items: center;
    transition: all .3s;

    &:hover {
      background-color: rgba(188, 155, 93, 0.5);
    }
  }
}

.nav-mobile {
  & > div {
    padding: 1.5rem;
    display: flex;
    align-items: center;
    transition: all .3s;

    //&:not(.hover-li):hover {
    //  background-color: rgba(188, 155, 93, 0.5);
    //}
  }

  .hover-li {
    padding: 0 !important;
    transition: all .3s;

    .product-item {
      &:hover {
        background-color: rgba(188, 155, 93, 0.5);
      }
    }

    //&:hover {
    //  background-color: transparent;
    //}
  }
}
</style>